* {
  margin: 0;
  padding: 0;
  font-size: 30px;
  text-transform: uppercase;
  font-family: 'Anton', sans-serif;
  box-sizing: border-box;
  color: var(--white);
  background: var(--black);
  --black: #1A1A1D;
  --light-black: #4E4E50;
  --white: #FEFFFF;
}

button,
input,
optgroup,
select,
textarea {
  border: none;
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;

}

input {
  all: unset;

}

input,
textarea,
button,
select,
div, span,
a {
  -webkit-tap-highlight-color: transparent;
}

::placeholder {
  color: var(--light-black)
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  min-width: 300px;
  min-height: 400px;
  padding: 30px;
  /* overflow-y: scroll; */

}